import React from "react";
import Avatar from "@material-ui/core/Avatar";

export default function Circle({ result }) {
  return (
    <Avatar
      style={{
        width: 20,
        height: 20,
        backgroundColor: result ? "#5cb85c" : "#EE2737",
        marginRight: 5
      }}
    >
      {result ? "V" : "D"}
    </Avatar>
  );
}
